"use client"

import { useEffect, useState } from "react"
import { useTheme } from "next-themes"
import { Moon, Sun } from "lucide-react"

export function ThemeToggle() {
    const [mounted, setMounted] = useState(false)
    const { theme, setTheme } = useTheme()

    useEffect(() => {
        setMounted(true)
    }, [])

    if (!mounted) {
        return (
            <button
                className="relative w-9 h-9 rounded-lg border-2 border-border bg-background flex items-center justify-center"
                aria-label="Toggle theme"
            >
                <div className="w-4 h-4" />
            </button>
        )
    }

    return (
        <button
            onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
            className="relative w-9 h-9 rounded-lg border-2 border-border bg-background hover:border-primary hover:bg-primary/5 transition-all duration-300 flex items-center justify-center group overflow-hidden"
            aria-label="Toggle theme"
        >
            <Sun className="w-4 h-4 text-foreground rotate-0 scale-100 transition-all duration-500 dark:-rotate-90 dark:scale-0 absolute" />
            <Moon className="w-4 h-4 text-foreground rotate-90 scale-0 transition-all duration-500 dark:rotate-0 dark:scale-100 absolute" />
        </button>
    )
}
